import React, { useState } from "react"

import { Container, FormContainer } from "./styles"

import Banner from "../../utils/Banner"
import BookCall from "../../utils/BookCall"

import { translate } from "../../utils/translate"
import SectionHighlight from "../../../components/pages/Solutions/SectionHighlight"
import FormGapAnalysis from "../../utils/Form/FormGapAnalysis"
import Button from "../../utils/Button"
import useScript from "../../../hooks/external-scripts"
import { useEffect } from "react"

export default function GapAnalysis() {
	useScript(
		"https://d335luupugsy2.cloudfront.net/js/rdstation-forms/stable/rdstation-forms.min.js",
		true
	)

	useScript(
		"new RDStationForms('quick-gap-analysis-1cb5379fca789fa3d15a', 'UA-156947544-1').createForm()",
		false
	)

	const openModal = () => setShowModal(true)
	const closeModal = () => setShowModal(false)
	const title = "Gap Analysis"

	return (
		<div>
			<Banner titlePage={"Gap Analysis"} />
			<Container
				className="container"
				dangerouslySetInnerHTML={{
					__html: translate(`solutions.${title}.content`),
				}}
			/>
			<div style={{ padding: "10px", textAlign: "center" }}>
				<br />
				<br />
				<FormContainer
					role="main"
					id="quick-gap-analysis-1cb5379fca789fa3d15a"
				/>
			</div>
			<Container
				className="container"
				dangerouslySetInnerHTML={{
					__html: translate(`solutions.${title}.complement`),
				}}
			/>

			<SectionHighlight
				methodContent={translate(`solutions.${title}.methodContent`)}
				advantageContent={translate(`solutions.${title}.advantageContent`)}
			/>
			<div className="container">
				<section className="book-call">
					<BookCall />
				</section>
			</div>
		</div>
	)
}
