import React from "react"

import Layout from "../components/Layout"

import GapAnalysis from "../components/pages/GapAnalysis"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

export default function Gap() {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "solutions",
			name: translate("breadCrumb.solutions"),
			isActive: true,
		},
		{
			url: "gap-analysis",
			name: "Gap Analysis",
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="GAP ANALYSIS"
				titleMetaDescription="GAP ANALYSIS"
				description="Buscando soluções em qualidade? A Kivalita Consulting pode te ajudar! Conheça os nossos serviços e cases de sucesso para atender o seu negócio."
			></SEO>
			<GapAnalysis />
		</Layout>
	)
}
