import styled from "styled-components"

export const Container = styled.div`
	section.introduction-section {
		text-align: center;
		margin-bottom: 32px;
		margin-top: 64px;
		font-family: "Montserrat", Sans-serif;
		color: #444;

		h3 {
			color: #000000;
			font-size: 12px;
			font-weight: 300;
			text-transform: uppercase;
			letter-spacing: 7px;
			font-family: "Montserrat", Sans-serif;
		}

		h2 {
			font-family: "Montserrat", Sans-serif;
			font-size: 35px;
			font-weight: 600;
			margin-top: 24px;
			position: relative;
			margin-bottom: 60px;

			::before {
				content: "";
				margin: 0 auto;
				position: absolute;
				height: 2px;
				width: 100px;
				background-color: #f4bf8c;
				bottom: -30px;
				left: 0;
				right: 0;
			}
		}

		p {
			font-size: 16px;
			font-weight: 300;
			line-height: 1.6em;
		}
	}

	.wrapper-content {
		font-family: "Montserrat", Sans-serif;
		color: #444;

		h2 {
			font-size: 25px;
			font-weight: 600;
			line-height: 1.4em;
			margin-top: 16px;
		}

		h3 {
			font-size: 20px;
			font-weight: 600;
			line-height: 1.4em;
			margin-top: 16px;
		}

		p {
			font-size: 16px;
			font-weight: 300;
			line-height: 1.6em;
		}

		.text-note {
			font-size: 16px;
			font-weight: 300;
			font-style: italic;
			line-height: 1.6em;
		}

		ul {
			font-size: 17px;
			font-weight: 300;
			padding-left: 24px;

			li {
				margin-top: 5px;
				padding-bottom: 5px;
			}
		}
	}

	.project {
		max-width: 500px;
		margin-left: 65ch;

		span {
			font-size: 16px;
			font-weight: 400;
			color: #555;
		}
	}

	.wraper-cta-left {
		padding-left: 32px;
		position: relative;

		img {
			border-radius: 4px;
			width: 100%;
		}

		a.button-cta {
			font-family: "Poppins", Sans-serif;
			font-size: 18px;
			font-weight: 600;
			color: #fff;
			background-color: #34e67f;
			border-radius: 5px;
			padding: 25px 50px 25px 50px;
			display: inline-block;
			position: relative;
			bottom: 30px;
			right: 24px;
		}
	}

	.wraper-cta-right {
		padding-right: 32px;
		position: relative;

		img {
			border-radius: 4px;
			width: 100%;
			margin-bottom: 32px;
		}

		a.button-cta {
			font-family: "Poppins", Sans-serif;
			font-size: 18px;
			font-weight: 600;
			color: #fff;
			background-color: #34e67f;
			border-radius: 5px;
			padding: 25px 50px 25px 50px;
			display: inline-block;
			position: absolute;
			bottom: -8px;
			right: 16px;
		}
	}

	.timeline {
		.block-arrow {
			background-color: #607b8b;
			width: 105%;
			height: 40px;
			display: inline-block;
			position: relative;
			line-height: 2.5;

			&:before {
				color: #f3f3f3;
				border-left: 10px solid;
				border-top: 20px solid transparent;
				border-bottom: 20px solid transparent;
				display: inline-block;
				content: "";
				position: absolute;
			}

			&:after {
				color: #607b8b;
				border-left: 10px solid;
				border-top: 20px solid transparent;
				border-bottom: 20px solid transparent;
				display: inline-block;
				content: "";
				position: absolute;
				right: -10px;
			}

			span {
				color: #fff;
				display: inline-block;
				text-align: center;
				width: 100%;
			}
		}

		.top-line {
			background-color: inherit;
			border-bottom: 1px solid #607b8b;
			width: 30px;
		}

		.box {
			background-color: inherit;
			border-left: 1px solid #607b8b;
			height: 200px;

			.text-inner {
				padding-top: 10px;
				margin-left: 1rem;
			}
		}
	}
`
export const FormContainer = styled.div`
	display: -webkit-inline-box;
`
